form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    max-width: 800px;
    margin: auto;
    background-image: linear-gradient(yellow, blue);
    padding: 20px;
  }
  form h2{
    font-size: 30px;
  }
  
  label {
    margin-bottom: 5px;
  }
  
  input, select {
    padding: 8px;
    width: 100%;
    box-sizing: border-box;
  }
  
  button {
    grid-column: span 2;
    padding: 10px 20px;
    background-color: rgb(255, 111, 0);
    color: white;
    border: none;
    cursor: pointer;
  }
  
  button:hover {
    background-color: rgb(0, 139, 28);
  }
  
  .error {
    border: 2px solid red;
  }
  
  .error-message {
    color: red;
    font-size: 0.9em;
    margin-top: 5px;
  }
  .check{
        display: flex;

  }
  .check input{
    width: 20px;
  }